import React, { Component } from 'react';

class Dropdown extends Component{
    constructor(props) {
        super(props);
        //if("auto_value" in props) this.state={ auto_value:props.auto_value,value:props.value,props:props.items};
        this.state={value:props.default_value,props:props.items};
        this.handleChange = this.handleChange.bind(this);

    }
    handleChange(e){
        if(this.props.value!==''){
        this.props.changeOption((this.props.level+1),e.target.value)
        //console.log(this.state.value)
        }
        else
        this.props.changeOption((this.props.level+1),e.target.value);
        this.setState({value:e.target.value});
    }
    static getDerivedStateFromProps(props,state){
        if(props.items!==state.props) return{value:props.value,props:props.items};
        return{};
    }
    render() {
        let value =this.state.value;
        if("value" in this.props){
            value= this.props.value;
        }

        return (
            <select onChange={this.handleChange} value={value} hidden={this.props.hidden==="1" && "hidden"}>
                <option value='' hidden="hidden">Alege ceva</option>
                {this.props.items.map(function(item,key) {
                    return <DropdownItem item={item} item_id={key} key={key}/>
                })
                }
            </select>
        );
    }
}

class DropdownItem extends Component{
    
    render() {

        return (
            <option value={this.props.item_id}>
                {this.props.item.name}
            </option>
        );
    }
}

export default Dropdown;