import React, { Component } from 'react';
import Selection from './Selection'

class Category extends Component{
    constructor(props){
        super(props);
        this.changeSelection = this.changeSelection.bind(this);
        this.outputValue = this.outputValue.bind(this);
        let proto ={value:null,name:null};
        this.state = {values:[proto,proto,proto,proto,proto,proto,proto,proto,proto,]}
    }

    changeSelection(selection_meta){
        //this.setState({selection:selection_meta});
        this.props.setGlobalVariables({
            name:this.props.category.name,
            selection:selection_meta
        });
    }

    outputValue(value,name,new_key){
        const values = this.props.category.costs.map((item,key) =>{
                if(new_key===key)
                    return {value:value,name:name};
                return this.state.values[key];
            }
        )
        this.setState({values:values});
        this.props.setOutputValues(values, this.props.category.name, this.props.category_id);
        //console.log(1);
    }


    render(){
        let selection;
        let auto_values='';
        //console.log(this.state);
        selection = this.props.category.costs.map((selection,key) =>
            <Selection expectedValue={this.state.values[key].value} globalVariables={this.props.globalVariables} default_values={auto_values} data={selection} selection_id={key} key={key} changeSelection={this.changeSelection} outputValue={this.outputValue}/>);
        return(
        <div>
            <h1>{this.props.category.name}</h1>
            {selection}
        </div>
        )
    }
}

export default Category