import React, { Component } from 'react';
import Dropdown from './Dropdown';



class MultipleSelection extends Component{

    constructor(props){
        super(props);
        this.state={
            selections:[],
            no_selections:0
        }
        this.addItem = this.addItem.bind(this);
        this.removeItem = this.removeItem.bind(this);

    }

    addItem(level,new_key){    
        //console.log("new key:");
        //console.log(new_key);
        let key;
        var selections= new Array(this.state.no_selections+1)
        for(key=0;key<=this.state.no_selections;key++){
            selections[key]=this.state.selections[key];
            if(key===this.state.no_selections){
                selections[key]=this.props.data.data[new_key];
            }
        }
        this.props.setOutputValues(selections, "servicii online", 1);

        console.log(selections)
        this.setState({
            selections:selections,
            no_selections:this.state.no_selections+1
        })

    }

    removeItem(item_no){
        let key;
        var selections= new Array(this.state.no_selections-1)
        for(key=0;key<=this.state.no_selections;key++){
            if(key<item_no)
                selections[key]=this.state.selections[key];
            if(key>item_no)
                selections[key-1]=this.state.selections[key];
        }
        //console.log("selections")
        //console.log(selections)
        //this.props.setOutputValues(values, this.props.category.name, 1);

        this.setState({
            selections:selections,
            no_selections:this.state.no_selections-1
        })
    }

    render(){
        let auto_value="";
        console.log(this.state);
        return(
        <div>
            <h1>{this.props.data.name}</h1>
            <Dropdown  value={auto_value} items={this.props.data.data} changeOption={this.addItem} /*level={0} /*globalVariables={this.props.globalVariables}*/ />
            { <MultipleSelectionsList selections={this.state.selections} removeItem={this.removeItem} no_selections={this.state.no_selections}/> }
            {/* <div>
                {this.state.selections.map(function(item,key) {
                    return <MultipleSelectionItem item={item} key={key} item_id={key} removeItem={this.removeItem}/>
                })
                }
            </div> */}
        </div>


        )
    }
}

class MultipleSelectionsList extends Component{

    constructor(props){
        super(props);
        this.state={
            selections:props.selections,
            no_selections:props.no_selections
        }
    }

    static getDerivedStateFromProps(props,state){
        if(props.selections!==state.selections)
            return {selections:props.selections,no_selections:props.no_selections}
    }

    render(){
        //console.log(1);
        //console.log(this.state);
        let items=[];
        let key;
        //if(this.state.selections!==[undefined]){
            for(key=0;key<this.state.no_selections;key++){
                //console.log(10)
                items[key] = <MultipleSelectionItem item={this.state.selections[key]} key={key} item_id={key} removeItem={this.props.removeItem}/>
            }
        //}
        //console.log(items)
           /* items =this.state.selections.map((item,key) =>
                 <MultipleSelectionItem item={item} key={key} item_id={key} removeItem={this.props.removeItem}/>
            )*/
        return(
            <div>
                {/* {this.props.selections.map(function(item,key) {
                    return <MultipleSelectionItem item={item} key={key} item_id={key} removeItem={this.props.removeItem}/>
                }) */}
                {items}
            </div>
        )
    }
}

class MultipleSelectionItem extends Component {
    constructor(props){
        super(props);
        this.handleChange = this.handleChange.bind(this);
        this.state={
            name:props.item.name,
            value:props.item.value,
            item_id:props.item_id
        }
    }

    handleChange(e){
        this.props.removeItem(this.props.item_id);
    }

    render(){
        return(
            <div><span><h5>{this.props.item.name}</h5><h6>{this.props.item.value}</h6></span><button onClick={this.handleChange} >X</button></div>
        )
    }

}



export default MultipleSelection;