import React, { Component } from 'react';
import { Chart } from "react-google-charts";
import './App.css';
import Category from './components/Category';
import MultipleSelection from './components/MultipleSelection';

import {data} from './data/categories.js';
import {subscription_services} from './data/subscription_services.js';

class App extends Component {
  constructor(props){
    super(props);
    let proto ={value:null,name:null}; let values={values:[proto,proto,proto,proto,proto,proto,proto,proto,proto,], name:null}
    this.state = {
      globalVariables:{
        locality:"",
        residence_type:""
      },
      values_arr:[values,values,values,values,values,values,values,values,values,values,values,values,values,values,]
    }
    this.getGlobalVariables = this.getGlobalVariables.bind(this);
    this.getOutputValues = this.getOutputValues.bind(this);
  }

  getGlobalVariables(new_state){
    let new_locality;
    let new_residence_type;
    if(new_state.name==="Cazare"){
      if(new_state.selection[0]!==undefined)
        new_locality = new_state.selection[0];
      if(new_state.selection[1]!==undefined)
        new_residence_type = new_state.selection[1];
    }
    this.setState({
      globalVariables:{
        locality:new_locality,
        residence_type:new_residence_type
      }
    })
  }

  getOutputValues(values,name,new_key){
    const values_arr = data.map((item,key) =>{
      if(new_key===key)
        return {values:values,name:name};
      //if(new_key!==1)
      return this.state.values_arr[key];
    })
    if(new_key===1)
      values_arr[1]={values:values,name:name}
    this.setState({values_arr:values_arr});
    //console.log(values_arr);
  }

  render() {
    console.log(this.state);
    let categories;
    categories = data.map((category,key) =>
      <Category globalVariables={this.state.globalVariables} category_id={key} category={category} key={key} setGlobalVariables={this.getGlobalVariables} setOutputValues={this.getOutputValues}/>
    );
    let disp_buff_values=[null];
    let index=0;
    let chart_data = [['From', 'To', 'Weight']];
    for(const values of this.state.values_arr){
      let sum=0;
      for(const value of values.values){
        if(value.name!==null){
          disp_buff_values[index]= <div key={index}><h3>{value.name}:{value.value}</h3>{"\n"}</div>;
          chart_data[index+1]=[values.name, value.name ,parseInt(value.value,10)];
          index++;
          sum=sum+parseInt(value.value,10)
        }
      }
      chart_data[index+1]=['Banet', values.name, sum];
      index++;
    }
    let chart;
    console.log(this.state.values_arr);
    if(index>=1) chart = <Chart
                          width={600}
                          height={'300px'}
                          chartType="Sankey"
                          /*loader={<div>Loading Chart</div>}*/
                          data={chart_data}
                          rootProps={{ 'data-testid': '1' }} />
    return ( 

      <div className = "App" >
        {chart}
        {/* {disp_buff_values} */}
        {categories}

        <MultipleSelection  data={subscription_services} key={1} setOutputValues={this.getOutputValues}/>

      </div>
    );
  }
}

export default App;